@import url(https://fonts.googleapis.com/css?family=Roboto:700);
@import url(https://fonts.googleapis.com/css?family=Roboto:700);
 nav{
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 15%;
    padding: 10px 100px;
    box-sizing: border-box;
    transition: .5s;
    background: rgba(0,0,0,0.8);

}
.logo img{
	float: left;
	width: 90px;
	height: 150px;
}

.logo img{
    height: 175px;
    width: 175px;
    transition: .5s;
}
nav ul{
    float: right;
    margin: 0;
    padding: -50px;
    display: flex;

}

nav ul li{
    list-style: none;
}

nav ul li .link{
   
    line-height: 80px;
    color: #fff;
    padding: 1px 10px;
    font-family: 'Roboto', sans-serif;
    text-decoration: none;
	text-transform: uppercase;
	font-size: 15px;

}

nav ul li a:active,
nav ul li a:hover{
    color: #fff;
    background: rgb(2, 109, 11,.8);
}

/* Change Scroll Position */

nav.black{
    background: rgba(0,0,0.0.8);
    height: 80px;
    padding: 15px 50px;
    
}

nav.black .logo img{
	width: 110px;
	height: 115px;
}

nav.black ul li .link{
    color: #fff;
    line-height: 45px;
    font-size: 17px;
}

@media (max-width:767px){
    nav{
       width: 100%;
       background: rgba(4,153,129,0.8);
    }
    nav .logo img{
        display: none;
        
    }
    nav.black ul li .link{
        padding: 15px 25px ;
        color: #000;
        line-height: 45px;
        font-size: 17px;
    }
    nav ul{
        float: left;
        margin: 0;
        padding: -20px;
    
    }
}
@media (max-width:528px){
    nav{
        width: 100%;

        background: rebeccapurple;
     }
}

@media (max-width:453px){
    nav{
        padding: 5px;
        width: 100%;
        background: rgba(4,153,129,0.8);
     }
     nav.black ul li .link{
        padding: 1px 20px; 
        color: #000;
        line-height: 45px;
        font-size: 17px;
    }
}
.gallery {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;  
  }
.gallery-col{
    margin: 25px;
}
.gallery img {

    width: 100%;
    height: auto;
    object-fit: cover;
  }

.gallery img:hover {
    transform: scale(1.1);
    opacity: 0.5;
} 

.slide-title {
    padding: 15px;
    text-align: center;
    color: rgb(207, 115, 9);
    text-transform: uppercase;
}

.slide-p{
    text-align: center;
    font-weight: bold;
}
.footer-links{

    background: #049981; 
    color: white;
    font-size: 12px;
    padding: 35px 0;

}

.footer-container-page{
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-gap: 10px;
    gap: 10px;
    align-items: flex-start;
    justify-content: center;
}
.footer-container-page ul{
    margin: 0 auto;
    list-style:none;
}

.footer-container-page h5{
    color: #343A40;
}

.footer-container-page ul li{
    line-height: 2.8;

}

.footer-container-page .icons-social{
    font-size: 1.2rem;
}
.footer-development{
    background: #343A40;
    color: white;
    padding: 3px 0;
    text-align: center;
}

/* CONTACT WHATSAPP*/

.float{
	position:fixed;
	width:50px;
	height:50px;
	bottom:40px;
	right:40px;
	background-color:#25d366;
	color:#FFF;
	border-radius:50px;
	text-align:center;
    font-size:30px;
	box-shadow: 2px 2px 3px #999;
  z-index:100;
}
.float:hover {
	text-decoration: none;
	color: #25d366;
  background-color:#fff;
}

.my-float{
	margin-top:auto;
}


@media (max-width:700px){
   
    .footer-container-page {
        text-align: center;
        grid-template-columns: repeat(2, 1fr);
    }
    .footer-container-page ul{
        list-style:none;
        }
}

@media (max-width:500px){

    .footer-container-page ul{
    list-style:none;
    }

    .footer-container-page{
        text-align: center;
        grid-template-columns: 1fr ;
    }

}
*{
