}
/* News Cards*/

.news-cards2 .card-right img{
    height: 100%;
    width: 80%;
    object-fit: cover;

}
.news-cards2{
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-gap: 25px;
    gap: 25px; /* espaciado entre columnas*/
